.AppleCareContainer{
    display: flex ;
    flex-wrap: wrap;
    gap: 60px;
    width: 100%;
}

.AppleCareCard{
    height:auto;
    width: 290px;
    box-shadow: rgba(17, 17, 26, 0.1) 0px 0px 16px;
    padding-bottom: 10px;
    cursor:grab;
    border: 0.1px solid #ebe1ed;
}

.AppleCareCard:hover {
    box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px, rgba(17, 17, 26, 0.05) 0px 8px 32px;
}

.AppleCareDescription{
    padding: 0px 10px;
    text-align: center;
}

.AppleCarePrice{
    margin-top: 10px;
    text-align: center;
    font-weight: 600;
    font-size: 18px;
}